
import Navbar from './navbar/index'
import Home from './home/index'
import 'bootstrap/dist/css/bootstrap.min.css';
import Curriculo from './curriculo';
import { useState } from 'react';

function App() {
    let [cont,setCont]= useState()
    let pages = [
    <Home/>,
    <Curriculo/>
    ]
    let pageReq = (param)=>{
   
        let local = (!param)?window.location.href.split('#'):param.split('#')
    
        switch (local[1]){
            case'Home':
                return 0;
            case 'Resume':
                return 1
            default: return 0
        }
        
    
    }
    let hUp = async (param)=> {
        const result = await pageReq(param)
        setCont(result)
    }

    return(
        <>
            <Navbar pOnClick={(param)=>hUp(param)}/>
            <div>
            {pages[cont?cont:0]}
            </div>

        </>

    )

}

export default App;
